import React from 'react';
import PropTypes from 'prop-types';
import Menu from './Menu';
import Menuitems from './Menuitems';

const Filterbyprice = ({filterprice}) => {
  return <>
   <div className='mt-5 container'>
        <div className='d-flex justify-content-around'>
            {/* <button className='btn btn-primary' onClick={( )=> setItems(Menu)}>All</button> */}
            <button className='btn btn-warning  mx-1' onClick={( )=> filterprice('300')}> Under 300</button>
            <button className='btn btn-warning mx-1'onClick={( )=> filterprice('100')}>Under 100</button>
            <button className='btn btn-warning mx-1' onClick={( )=> filterprice('200')}>Under 200</button>
            {/* <button className='btn btn-warning' onClick={( )=> filterItem('Chinese')}>Chinese</button> */}
        </div>
    </div>

  </>;
};

Filterbyprice.propTypes = {};

export default Filterbyprice;
