import React, {useState} from 'react';
import Menu from './Menu';
import Menuitems from './Menuitems';
import Filterbyfood from './Filterbyfood';
import Filterbyprice from './Filterbyprice';
import { tab } from '@testing-library/user-event/dist/tab';
import './tab.css'

const GalleryReact = () => {
  const [food, setFood] = useState("Filter by food");
  const [price, setPrice] = useState("Filter by price");
    const allCat = [...new Set(Menu.map(elem => elem.category)),"All"];
    const [items, setItems] = useState(Menu);
    const [show,setshow] = useState(false);
    const [show1,setshow1] = useState(false);
    const [allCatvalue,setallCatvalue] = useState(allCat);
    const filterItem = (category) => {
        if(category === "All"){
            setItems(Menu);
        }
        else{
        const updatedcategory = Menu.filter((item) =>{return item.category === category});
        setItems(updatedcategory);
        }
    }
    const showfood = () => {
      setshow(!show);
      if (!show) {
        
        setFood("Remove filter by food");
      }
      else{
        setFood("Filter by food");
        setItems(Menu);
      }
      console.log(show);
    }
    const showprice = () => {
      setshow1(!show1);
      if (!show1) {
        
        setPrice("Remove filter by price");
      }
      else{
        setPrice("Filter by price");
        setItems(Menu);
      }
      console.log(show1);
    }
    const filterprice = (price) => {
        const updatedprice = Menu.filter((item) =>{return item.price <= price});
        console.log(updatedprice);
        setItems(updatedprice.sort((a,b) => b.price - a.price));
        console.log(items);
    }
  return <div>
      <div className='container' id='maxx' style={{backgroundColor : '#0DCAF0'}} >

    <h1 className='text-center' >Food gallery</h1>
      </div>
    
    <button className='btn btn-danger float-start mx-2' id='bt1' onClick={showfood}>{food}</button>
    <button className='btn btn-info float-end mx-2' id='bt2' onClick = {showprice} >{price}</button>
    <br></br>
    

       {(show &&  <Filterbyfood filterItem = {filterItem} allCatvalue = {allCatvalue}></Filterbyfood>) }
       
       {show1 &&  <Filterbyprice filterprice = {filterprice}></Filterbyprice>}
    
    <div className='container-fluid mt-5'>

    <div className='row'>

    <div className='col-11 mx-auto'>
    <div className='row my-5 align-middle'>

    
     <Menuitems items={items}></Menuitems>
    
    </div>

    </div>
    </div>
    </div>
  </div>
};

export default GalleryReact;
